body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #34495e;
}

.form-container {
  background: #191919;
  padding: 50px 20px;
}

body, #root, .app {
  height: 100%;
}

.app {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.form-control {
  background: #191919;
  border: 2px solid #3498db;
  color: #fff;
  border-radius: 25px;
  -webkit-transition: 0.25s;
  transition: 0.25s;
  width: 60%;
  margin: 0 auto;
  padding: 20px;
}

.form-control:focus {
  width: 80%;
  background: #191919;
  border-color: #2ecc71;
  color: #fff;
}

.btn.btn-outline-success {
  border-width: 2px;
  border-radius: 25px;
  width: 60%;
  margin: 0 auto;
}

h2 {
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 20px;
}
